import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Collapse from 'react-bootstrap/Collapse';

function Transition() {
  const [open, setOpen] = useState(false);

  return (
    <>
      <Button
        onClick={() => setOpen(!open)}
        aria-controls="example-collapse-text"
        aria-expanded={open}
      >
        click
      </Button>
      <div style={{ minHeight: '150px' }}>
        <Collapse in={open} dimension="width">
          <div id="example-collapse-text">
            <Card body style={{ width: '400px' }}>
            En informtica, un bus es un sistema digital que permite 
            la transferencia de datos entre los componentes de un sistema 
            informtico. Est formado por cables o pistas en un circuito 
            impreso, dispositivos como resistores y condensadores, 
            adems de circuitos integrados.
            </Card>
          </div>
        </Collapse>
      </div>
    </>
  );
}

export default Transition;